.contact {
  width: 90%;
  margin: 0 auto;
  padding-bottom: 2rem;
}

/* FORM */

.form {
  display: flex;
  flex-direction: column;
  padding: 1rem 2rem;
  max-width: 500px;
  margin: 0 auto;
}

.form input,
.form textarea {
  font: inherit;
  border: none;
  padding: 1rem;
  margin: 0.5rem 0;
  border-bottom: 1px solid var(--verde-fluor);
  background: none;
  background-color: #3b4353;
  color: var(--gris-font);
  border-radius: 5px;
}

.form textarea {
  resize: none;
  height: 200px;
}

.form input::placeholder,
.form textarea::placeholder {
  color: #fff;
}

.form input[type="submit"] {
  background-color: var(--verde-oscuro);
  color: #fff;
  border-radius: 5px;
}

.form input[type="submit"]:hover {
  background-color: var(--verde-fluor);
}

.form-title {
  margin-bottom: 0.5rem;
}

.enviado {
  background-color: yellowgreen;
  width: 60%;
  margin: 0 auto;
  color: #fff;
  padding: 0.5rem;
  transition: all 0.6s ease-in-out;
}

.error {
  background-color: rgb(219, 31, 31);
  width: 90%;
  margin: 0 auto;
  color: #fff;
  padding: 0.5rem;
  transition: all 0.6s ease-in-out;
}

.error-pattern {
  font-size: 12px;
  background-color: rgb(219, 31, 31);
  width: 90%;
  margin: 0.5rem auto;
  color: #fff;
  padding: 0.2rem;
  transition: all 0.6s ease-in-out;
}